*{
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
    font-size: 20px;
    outline: none;
    color: orangered;
    transition: 300ms;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
body{
    width: 100%;
    height: 100vh;
    background: #fff;
    position: relative;
    display: flex;
    justify-content: center;
    padding: 30px 0px;
    h1{
        font-size: 20px;
        text-align: center;
        font-weight: 600;
        padding: 40px 0px;
        width: 300px;
    }
    .calculator{
        width: 300px;
        height: 70vh;
        background: orangered;
        border: 1px solid orangered;
        border-radius: 5px;
        overflow: hidden;
        box-shadow: 0px 0px 10px orangered;
        display: grid;
        grid-template-areas: 
        "history history history history"
        "display display display display"
        "clear clear divide multiply"
        "btn7 btn8 btn9 subtract"
        "btn4 btn5 btn6 add"
        "btn1 btn2 btn3 equal"
        "btn0 btn0 decimal equal";
        @media only screen and (max-width: 500px) {
                width: 300px;
        }
        #display{
            height: 30px;
            text-align: right;
            font-size: 40px;
            padding: 10px;
            color: #fff;
        }
        #history{
            height: 30px;
            text-align: right;
            font-size: 25px;
            padding: 10px;
            color: #fff;
        }
        button{
            border: 1px solid orangered;
            transition: 200ms;
            color: #000;
            &:hover{
                background: orangered;
                color: #fff;
            }
        }
    }
}

$areas: (history,display,clear,divide,multiply,subtract,add,equal,decimal,btn0,bnt1,btn2,btn3,btn4,btn5,btn6,btn7,btn8,btn9);
@each $area in $areas {
  ##{$area} {
    grid-area: #{$area};
  }
}

